export default {
	name: "shiPment",
	props: {
		editShow: {
			type: Boolean,
			value: false
		},
		dataInfo: {
			type: Object,
			default: undefined
		}
	},
	data() {
		return {
			reverse: true,
			activities: []
		};
	},
	mounted() {
		// You have not made any changes 您没有做任何改动
		let thif = this;
		this.dataInfo["details"].forEach(function(item) {
			thif.activities.push({
				content: item["message"] + " \n" + item["location"],
				timestamp: item["date"]
			});
		});
	},
	methods: {
		closeEdit() {
			this.$emit("editClose");
		}
	}
};
