import ShiPment from "~/components/order/shipment";
export default {
  name: "orderRight",
  components: {
    ShiPment
    // orderItem
  },
  head() {
    return {
      script: [
        {
          src:
            "https://cdnjs.cloudflare.com/ajax/libs/clipboard.js/2.0.0/clipboard.min.js"
        }
      ]
    };
  },
  data() {
    return {
      showshipmentState: false,
      itemList: [],
      orderData: null,
      keepInterval: 0,
      countDown: "",
      timeObj: null,
      countDownShow: false,
      reasonsList: [],
      cancelActive: "no",
      reasonsText: "",
      reviewText: "",
      cancelMaskingShow: false,
      reviewMaskingShow: false,
      ifStatus: "", //记录订单状态
      payBtnShow: false, //支付组按钮显示状态
      payText: "", //支付组按钮显示文本
      payBtnType: 1, //支付按钮点击执行的方法
      cancelBtnShow: false, //取消按钮显示状态
      cancelText: "", //取消订单按钮显示文本
      cancelBtnType: 1, //取消按钮点击执行方法,
      showshipmentdata: {},
      receiptOrderLoading: false,
      addReviewsLoading: false,
      rating: null,
      reviewOrderId: ''
    };
  },
  mounted() {
    this.getOrderDetail();
  },
  methods: {
    getOrderDetail() {
      let thif = this;
      this.$apis
        .orderDetail(
          { id: this.$route.query.orderId },
          this.$store.getters.getApiHeader
        )
